import { Component } from "react";
import Calender from "./calender";
import AddClient from "./add_client";
class Header extends Component{
    constructor(props){
        super(props);
        this.state={
            onOpenContainNavBottom : -1,//-1 khong mo. 0 mo button. 1 mo calender,2 mo calender,3 khong mo.
            isNotifiCovid:true,
            isStickyMainNav:false,
            isHeaderOnMobile:false,
            isOpenUserBtn:false,
            isShowNavCenter:true,
            openMobileSearch:false,
            navCenterBottom:[
                {value:[{title:'Nhận phòng',description:'Thêm ngày'},
                        {title:'Trả phòng',description:'Thêm ngày'},
                        {title:'Khách',description:'Thêm khách'}
                    ]},
                {value:[{title:'Ngày',description:'Thêm thời điểm bạn muốn tham gia'}
                    ]}
            ],
            indexActiveNavCenterBottom : 0,
        }
    }
    onChangeIndexNavCenterTop = (index)=>{
        var {indexActiveNavCenterBottom}=this.state;
        if(indexActiveNavCenterBottom!==index){
            this.setState({
                indexActiveNavCenterBottom : index
            })
        }
        
    }
    closeNotifiCovid = ()=>{
        this.setState({isNotifiCovid:false});
        
    }
    toggleUserBtn = ()=>{
        this.setState({
            isOpenUserBtn: !this.state.isOpenUserBtn
        })
    }
    closeUserBtn =()=>{
        this.setState({
            isOpenUserBtn :false
        })
    }
    stickNav = ()=>{
            this.setState({
                isStickyMainNav: true,
                isHeaderOnMobile:true
            })
        }
    moveStickNav = ()=>{
        this.setState({
            isStickyMainNav:false,
            isHeaderOnMobile:false
        })
    }
    showNavCenter = ()=>{
        this.setState({
            isShowNavCenter:true
        })
    }
    renderNavCenterBottom = (index)=>{
        var {navCenterBottom}=this.state;
        var result = [];
        if(index === 0){
            result= navCenterBottom[index].value.map((element,index)=>{
                return <li key={index} onClick={()=>this.onOpenContainNavBottom(index+1)} style={{flexBasis:'33.3333%'}}>
                        <p><b>{element.title}</b></p>
                        <p>{element.description}</p>
                </li>
            })
        }else{
            result= navCenterBottom[index].value.map((element,index)=>{
                return <li key={index} onClick={()=>this.onOpenContainNavBottom(index+1)} style={{flexBasis:'100%'}}>
                        <p><b>{element.title}</b></p>
                        <p>{element.description}</p>
                </li>
            })
        }
        return result;
    }
    openSelectLanguageMoney = ()=>{
        this.props.openSelectLanguageMoney(true)
    }
    onOpenContainNavBottom = (data)=>{
        if(data!== this.state.onOpenContainNavBottom){
            this.setState({
                onOpenContainNavBottom:data
            })
        }else{
            this.setState({
                onOpenContainNavBottom:-1
            })
        }
        
    }
    toggleMobileSearch = ()=>{
        this.setState({
            openMobileSearch: !this.state.openMobileSearch
        })
    }
    closeMobileSearch =()=>{
        this.setState({
            openMobileSearch: !this.state.openMobileSearch
        })
    }
    render(){
        // var colorwhite='white'
        // var color222= '#222'
        // var primaryColor = '#FF385C'
        var color717171='#717171'
        var {isNotifiCovid,isStickyMainNav,isOpenUserBtn,isShowNavCenter,indexActiveNavCenterBottom,isHeaderOnMobile,openMobileSearch}= this.state;
        var  prventLink = function(){
            var lista = document.querySelectorAll('a');
            for(let i=0;i<lista.length;i++){
                lista[i].addEventListener('click',(e)=>{e.preventDefault()})
            }
        }
        window.addEventListener('load',prventLink);
        
        window.onscroll = ()=>{
            let notifiCovid = document.querySelector('.notifi_covid');
            if(document.documentElement.scrollTop>=notifiCovid.clientHeight && isStickyMainNav===false){//nếu kéo xuống và chưa hiện
                this.stickNav();
            }
            if(document.documentElement.scrollTop===0 && isStickyMainNav===true){
                this.moveStickNav();
            }
            if(document.documentElement.scrollTop===0){//hiện khi đang ẩn
                if(isShowNavCenter===false){
                    this.showNavCenter()
                }
            }
            if(document.documentElement.scrollTop>0){//ẩn
                if(isShowNavCenter===true){
                    this.setState({
                        isShowNavCenter:false
                    })
                }
            }
        }
        var onOpenContainNavBottom = this.state.onOpenContainNavBottom;
        return(
            <div className="container">
                <div className="notifi_covid top font-15 text-center" style={{display: isNotifiCovid===true ? 'block' : 'none'}}>
                    <a className="color-a3a3a3 p-20-0 hover-o-8" href='!#'>Nhận thông tin mới nhất về biện pháp ứng phó COVID-19 của chúng tôi </a>
                    <span className="pull-right color-white p-20 close_notify_covid" onClick={this.closeNotifiCovid}>X</span>
                </div>
                <div className="container__header relative">
                    <div className={isStickyMainNav===true ? "main__nav stickNav" : "main__nav"} >
                        <div className={isShowNavCenter===true ? "main__nav__left show": "main__nav__left"}>
                            <svg width="102" height="32" fill="currentcolor"  >
                                <path d="M29.24 22.68c-.16-.39-.31-.8-.47-1.15l-.74-1.67-.03-.03c-2.2-4.8-4.55-9.68-7.04-14.48l-.1-.2c-.25-.47-.5-.99-.76-1.47-.32-.57-.63-1.18-1.14-1.76a5.3 5.3 0 00-8.2 0c-.47.58-.82 1.19-1.14 1.76-.25.52-.5 1.03-.76 1.5l-.1.2c-2.45 4.8-4.84 9.68-7.04 14.48l-.06.06c-.22.52-.48 1.06-.73 1.64-.16.35-.32.73-.48 1.15a6.8 6.8 0 007.2 9.23 8.38 8.38 0 003.18-1.1c1.3-.73 2.55-1.79 3.95-3.32 1.4 1.53 2.68 2.59 3.95 3.33A8.38 8.38 0 0022.75 32a6.79 6.79 0 006.75-5.83 5.94 5.94 0 00-.26-3.5zm-14.36 1.66c-1.72-2.2-2.84-4.22-3.22-5.95a5.2 5.2 0 01-.1-1.96c.07-.51.26-.96.52-1.34.6-.87 1.65-1.41 2.8-1.41a3.3 3.3 0 012.8 1.4c.26.4.45.84.51 1.35.1.58.06 1.25-.1 1.96-.38 1.7-1.5 3.74-3.21 5.95zm12.74 1.48a4.76 4.76 0 01-2.9 3.75c-.76.32-1.6.41-2.42.32-.8-.1-1.6-.36-2.42-.84a15.64 15.64 0 01-3.63-3.1c2.1-2.6 3.37-4.97 3.85-7.08.23-1 .26-1.9.16-2.73a5.53 5.53 0 00-.86-2.2 5.36 5.36 0 00-4.49-2.28c-1.85 0-3.5.86-4.5 2.27a5.18 5.18 0 00-.85 2.21c-.13.84-.1 1.77.16 2.73.48 2.11 1.78 4.51 3.85 7.1a14.33 14.33 0 01-3.63 3.12c-.83.48-1.62.73-2.42.83a4.76 4.76 0 01-5.32-4.07c-.1-.8-.03-1.6.29-2.5.1-.32.25-.64.41-1.02.22-.52.48-1.06.73-1.6l.04-.07c2.16-4.77 4.52-9.64 6.97-14.41l.1-.2c.25-.48.5-.99.76-1.47.26-.51.54-1 .9-1.4a3.32 3.32 0 015.09 0c.35.4.64.89.9 1.4.25.48.5 1 .76 1.47l.1.2c2.44 4.77 4.8 9.64 7 14.41l.03.03c.26.52.48 1.1.73 1.6.16.39.32.7.42 1.03.19.9.29 1.7.19 2.5zM41.54 24.12a5.02 5.02 0 01-3.95-1.83 6.55 6.55 0 01-1.6-4.48 6.96 6.96 0 011.66-4.58 5.3 5.3 0 014.08-1.86 4.3 4.3 0 013.7 1.92l.1-1.57h2.92V23.8h-2.93l-.1-1.76a4.52 4.52 0 01-3.88 2.08zm.76-2.88c.58 0 1.09-.16 1.57-.45.44-.32.8-.74 1.08-1.25.25-.51.38-1.12.38-1.8a3.42 3.42 0 00-1.47-3.04 2.95 2.95 0 00-3.12 0c-.44.32-.8.74-1.08 1.25a4.01 4.01 0 00-.38 1.8 3.42 3.42 0 001.47 3.04c.47.29.98.45 1.55.45zM53.45 8.46c0 .35-.06.67-.22.93-.16.25-.38.48-.67.64-.29.16-.6.22-.92.22-.32 0-.64-.06-.93-.22a1.84 1.84 0 01-.67-.64 1.82 1.82 0 01-.22-.93c0-.36.07-.68.22-.93.16-.3.39-.48.67-.64.29-.16.6-.23.93-.23a1.84 1.84 0 011.6.86 2 2 0 01.21.94zm-3.4 15.3V11.7h3.18v12.08h-3.19zm11.68-8.9v.04c-.15-.07-.35-.1-.5-.13-.2-.04-.36-.04-.55-.04-.89 0-1.56.26-2 .8-.48.55-.7 1.32-.7 2.31v5.93h-3.19V11.69h2.93l.1 1.83c.32-.64.7-1.12 1.24-1.48a3.1 3.1 0 011.81-.5c.23 0 .45.02.64.06.1.03.16.03.22.06v3.2zm1.28 8.9V6.74h3.18v6.5c.45-.58.96-1.03 1.6-1.38a5.02 5.02 0 016.08 1.31 6.55 6.55 0 011.6 4.49 6.96 6.96 0 01-1.66 4.58 5.3 5.3 0 01-4.08 1.86 4.3 4.3 0 01-3.7-1.92l-.1 1.57-2.92.03zm6.15-2.52c.57 0 1.08-.16 1.56-.45.44-.32.8-.74 1.08-1.25.26-.51.38-1.12.38-1.8 0-.67-.12-1.28-.38-1.79a3.75 3.75 0 00-1.08-1.25 2.95 2.95 0 00-3.12 0c-.45.32-.8.74-1.09 1.25a4.01 4.01 0 00-.38 1.8 3.42 3.42 0 001.47 3.04c.47.29.98.45 1.56.45zm7.51 2.53V11.69h2.93l.1 1.57a3.96 3.96 0 013.54-1.89 4.1 4.1 0 013.82 2.44c.35.76.54 1.7.54 2.75v7.24h-3.19v-6.82c0-.84-.19-1.5-.57-1.99-.38-.48-.9-.74-1.56-.74-.48 0-.9.1-1.27.32-.35.23-.64.52-.86.93a2.7 2.7 0 00-.32 1.35v6.92h-3.16zm12.52 0V6.73h3.19v6.5a4.67 4.67 0 013.73-1.89 5.02 5.02 0 013.95 1.83 6.57 6.57 0 011.59 4.48 6.95 6.95 0 01-1.66 4.58 5.3 5.3 0 01-4.08 1.86 4.3 4.3 0 01-3.7-1.92l-.09 1.57-2.93.03zm6.18-2.53c.58 0 1.09-.16 1.56-.45.45-.32.8-.74 1.09-1.25.25-.51.38-1.12.38-1.8a3.42 3.42 0 00-1.47-3.04 2.95 2.95 0 00-3.12 0c-.44.32-.8.74-1.08 1.25a3.63 3.63 0 00-.38 1.8 3.42 3.42 0 001.47 3.04c.47.29.95.45 1.55.45z"></path>
                            </svg>
                            <svg width="30" height="32" fill="currentcolor"><path d="M29.24 22.68c-.16-.39-.31-.8-.47-1.15l-.74-1.67-.03-.03c-2.2-4.8-4.55-9.68-7.04-14.48l-.1-.2c-.25-.47-.5-.99-.76-1.47-.32-.57-.63-1.18-1.14-1.76a5.3 5.3 0 00-8.2 0c-.47.58-.82 1.19-1.14 1.76-.25.52-.5 1.03-.76 1.5l-.1.2c-2.45 4.8-4.84 9.68-7.04 14.48l-.06.06c-.22.52-.48 1.06-.73 1.64-.16.35-.32.73-.48 1.15a6.8 6.8 0 007.2 9.23 8.38 8.38 0 003.18-1.1c1.3-.73 2.55-1.79 3.95-3.32 1.4 1.53 2.68 2.59 3.95 3.33A8.38 8.38 0 0022.75 32a6.79 6.79 0 006.75-5.83 5.94 5.94 0 00-.26-3.5zm-14.36 1.66c-1.72-2.2-2.84-4.22-3.22-5.95a5.2 5.2 0 01-.1-1.96c.07-.51.26-.96.52-1.34.6-.87 1.65-1.41 2.8-1.41a3.3 3.3 0 012.8 1.4c.26.4.45.84.51 1.35.1.58.06 1.25-.1 1.96-.38 1.7-1.5 3.74-3.21 5.95zm12.74 1.48a4.76 4.76 0 01-2.9 3.75c-.76.32-1.6.41-2.42.32-.8-.1-1.6-.36-2.42-.84a15.64 15.64 0 01-3.63-3.1c2.1-2.6 3.37-4.97 3.85-7.08.23-1 .26-1.9.16-2.73a5.53 5.53 0 00-.86-2.2 5.36 5.36 0 00-4.49-2.28c-1.85 0-3.5.86-4.5 2.27a5.18 5.18 0 00-.85 2.21c-.13.84-.1 1.77.16 2.73.48 2.11 1.78 4.51 3.85 7.1a14.33 14.33 0 01-3.63 3.12c-.83.48-1.62.73-2.42.83a4.76 4.76 0 01-5.32-4.07c-.1-.8-.03-1.6.29-2.5.1-.32.25-.64.41-1.02.22-.52.48-1.06.73-1.6l.04-.07c2.16-4.77 4.52-9.64 6.97-14.41l.1-.2c.25-.48.5-.99.76-1.47.26-.51.54-1 .9-1.4a3.32 3.32 0 015.09 0c.35.4.64.89.9 1.4.25.48.5 1 .76 1.47l.1.2c2.44 4.77 4.8 9.64 7 14.41l.03.03c.26.52.48 1.1.73 1.6.16.39.32.7.42 1.03.19.9.29 1.7.19 2.5z"></path>
                            </svg>
                        </div>
                        <div className={isShowNavCenter===true ? "main__nav__center show": "main__nav__center"}>
                            <div className="main__nav__center-ontop" style={{display: isShowNavCenter===true ? '':'none'}}>
                                <a href='!#' className={indexActiveNavCenterBottom === 0 ? "noi_o active color-white font-16" : "noi_o color-white font-16"} onClick={()=>{this.onChangeIndexNavCenterTop(0)}} >Nơi ở</a>
                                <a href='!#' className={indexActiveNavCenterBottom === 1 ? "trai_nghiem active color-white font-16 " : "trai_nghiem color-white font-16"} onClick={()=>{this.onChangeIndexNavCenterTop(1)}}>Trải nghiệm</a>
                                <a href='!#' className="trai_nghiem_truc_tuyen color-white font-16">Trải nghiệm trực tuyến</a>
                            </div>
                            <div className="main__nav__center-onbottom" style={{display: isShowNavCenter===true ? '':'none'}}>
                                <div className="main__nav__center-onbottom-btn">
                                    <div className="nav__center-onbottom-btn-dia_diem" onClick={()=>{this.onOpenContainNavBottom(0)}} style={{flexBasis: indexActiveNavCenterBottom===0 ? '25%':'50%'}}>
                                        <p><b>Địa điểm</b></p>
                                        <p>Bạn sắp đi đâu</p>
                                    </div>
                                    <ul className="list-btn-select" style={{flexBasis:indexActiveNavCenterBottom=== 0 ?'75%' :'50%'}}>
                                        {this.renderNavCenterBottom(indexActiveNavCenterBottom)}
                                    </ul>
                                    <span className="nav-onbottom-btn-icon"><i className="fas fa-search"></i></span>
                                
                                </div>
                                <div className="main__nav__center-onbottom-container" style={{display:(onOpenContainNavBottom > -1 && onOpenContainNavBottom <4) ? '' : 'none',width:(onOpenContainNavBottom === 0 || onOpenContainNavBottom===3) ? '':'100%',right: onOpenContainNavBottom===3 ? '0':''}}>
                                    <div className="nav__center-dia_diem" style={{display:onOpenContainNavBottom === 0 ?'block':'none'}}>
                                        <div className="title">MỌI LÚC, MỌI NƠI</div>
                                        <div className="dia_diem-btn">
                                            <a href='/#' onClick={(e)=>{e.preventDefault()}} className="dia_diem-link"><span>Tìm kiếm linh hoạt</span> <i className="fas fa-chevron-right"></i></a>
                                        </div>
                                    </div>
                                    <Calender openCalender={onOpenContainNavBottom}></Calender>
                                    <AddClient openAddClient={onOpenContainNavBottom}></AddClient>
                                </div>
                            </div>
                            <div className="nav__center-btn-ontop" onClick={this.showNavCenter} style={{display:isShowNavCenter===true ? 'none':''}}>
                                <p><b>Bắt đầu tìm kiếm</b></p>
                                <span className="nav__center-btn-ontop-icon"><i className="fas fa-search"></i></span>
                            </div>
                        </div>
                        <div className={isShowNavCenter===true ? "main__nav__right show": "main__nav__right"}>
                            <div className="main__nav__right-8">
                                <a href="!#" onClick={this.openSelectLanguageMoney} className="select_language font-w-bold p-12-12" ><i className="fas fa-globe"></i></a>
                                <a href="!#" className="become_host font-w-bold p-12-12" >Trở thành chủ nhà</a>
                            </div>
                            <div className="main__nav__right-2">
                                <button className="user_btn" onClick={this.toggleUserBtn} style={{color:color717171,boxShadow: isOpenUserBtn===true ? '0px 3px 3px #d0c8c8' : ''}}>
                                    <i className="fas fa-bars"></i>
                                    <i className="fas fa-user"></i>
                                </button>
                                <div className="user_btn_container" style={{display: isOpenUserBtn === true ?'block' :''}}>
                                    <div className="user_btn_register active">Đăng ký</div>
                                    <div className="user_btn_login">Đăng nhập</div>
                                    <hr />
                                    <div className="user_btn_rent">Cho thuê nhà</div>
                                    <div className="user_btn_experience">Tổ chức trải nghiệm</div>
                                    <div className="user_btn_help">Trợ giúp</div>
                                </div>
                            </div>
                        </div>    
                    </div>
                    <div className="container__header-center">
                        <p>Bạn chưa biết nên đi đâu? tuyêt!</p>
                        <a href="/#">Tìm kiếm linh hoạt</a>
                    </div>
                    <div className={isHeaderOnMobile===true ? "header__on_mobile sticky" :"header__on_mobile "} style={{bottom: openMobileSearch===true ?'0':''}}>
                        <div className="header__on_mobile-search">
                            <div className="header__on_mobile-btn" onClick={this.toggleMobileSearch}>
                                <i className="fas fa-search"></i> Ban sắp đi đâu
                            </div>
                            <div className="header__on_mobile-search-process" style={{display: openMobileSearch === true ? 'block':'none'}}>
                                <div className="header__on_mobile-search-process-ontop">
                                    <span className="close-mobile-search-process"><i onClick={this.closeMobileSearch} className="fas fa-chevron-left"></i></span>
                                    <div className="input-mobile-sẻarch">
                                        <input type="text" placeholder="Bạn sắp đi đâu ?" />
                                    </div>
                                </div>
                                <p className="title-process">Mọi lúc, mọi nơi</p>
                                <div className="mobile-search-process-link">
                                    <a href="/#">
                                    <span>Tìm kiếm linh hoạt</span>
                                    <i className="fas fa-chevron-right"></i>
                                    </a>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                
            </div>
        )
    }
}
export default Header;